@import url(https://fonts.googleapis.com/css?family=Work+Sans);
* {
    padding: 0;
    margin: 0;
}

body {
    font-size: 18px;
    font-family: 'Work Sans', sans-serif;
    line-height: 1.5rem;
}

.container {
    display: flex;
}
.vertical-menu {
  flex: 1 1;
  width: 200px;
  margin: 1rem; }
  .vertical-menu ul {
    list-style: none; }
  .vertical-menu a {
    background-color: #eee;
    color: black;
    display: block;
    padding: 12px;
    margin-bottom: 1px;
    text-decoration: none; }
    .vertical-menu a:hover {
      background-color: #ccc; }
    .vertical-menu a:active, .vertical-menu a .active {
      background-color: #4CAF50;
      color: white; }
  .vertical-menu .icon {
    height: 20px;
    vertical-align: middle;
    margin-right: 1rem; }

.shift {
    flex: 1 1;
    margin-left: 1rem;
}
